import { Component } from '@angular/core';
import {
  ActionPerformed,
  PushNotificationSchema,
  PushNotifications,
  Token,
} from '@capacitor/push-notifications';
import { Platform } from '@ionic/angular';
import { OneSignal } from '@awesome-cordova-plugins/onesignal/ngx';


@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent {
  constructor(private platform: Platform, private oneSignal: OneSignal) {
      this.platform.ready().then(() => {
        //this.registerPush();
        setTimeout(() => {
          //this.registerPush();
          this.initOneSignal();  
        }, 10000);
        
      });
  }



  private initOneSignal(){
    
    this.oneSignal.startInit('47b4e360-cb13-48fa-90e7-409da7b91164', '969634075809');
    
    this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.InAppAlert);

    this.oneSignal.handleNotificationReceived().subscribe((jsondata) => {
    // do something when notification is received
    console.log(JSON.stringify(jsondata));
    });

    this.oneSignal.handleNotificationOpened().subscribe(() => {
      // do something when a notification is opened
    });

    this.oneSignal.getIds().then(res => console.log(JSON.stringify(res)));

    this.oneSignal.endInit();

  }

  private registerPush() {
    console.log('Initializing HomePage');

    // Request permission to use push notifications
    // iOS will prompt user and return if they granted permission or not
    // Android will just grant without prompting
    PushNotifications.requestPermissions().then(result => {
      if (result.receive === 'granted') {
        // Register with Apple / Google to receive push via APNS/FCM
        PushNotifications.register();
      } else {
        // Show some error
      }
    });

    // On success, we should be able to receive notifications
    PushNotifications.addListener('registration',
      (token: Token) => {
        alert('Push registration success, token: ' + token.value);
      }
    );

    // Some issue with our setup and push will not work
    PushNotifications.addListener('registrationError',
      (error: any) => {
        alert('Error on registration: ' + JSON.stringify(error));
      }
    );

    // Show us the notification payload if the app is open on our device
    PushNotifications.addListener('pushNotificationReceived',
      (notification: PushNotificationSchema) => {
        console.log('Push received: ' + notification.title +" "+ notification.body);
        alert('Push received: ' + notification.title +" "+ notification.body);
        
      }
    );

    // Method called when tapping on a notification
    PushNotifications.addListener('pushNotificationActionPerformed',
      (notification: ActionPerformed) => {
        console.log('Push received Performed: ' + notification.notification.title +" "+ notification.notification.body);
        alert('Push action performed: ' + JSON.stringify(notification));
      }
    );
  }
}
